import React, { useState, useEffect } from 'react'
import '../Signup/SignUp.css'
import logo from '../../Pages/Login/logo.png'
import { SocialIcon } from 'react-social-icons'
import {auth, Gauth, fb} from '../../Firebase/config'
import { Link, useHistory } from 'react-router-dom'
import Loader from '../../Components/Loader/Loader'
function SignUp() {

    const [email, setEmail] = useState('')
    const [paswrd, setPaswrd] = useState('')
    const [fname, setFname] = useState('')
    const [lname, setLname] = useState('')
    const [cpaswrd, setCpaswrd] = useState('')
    const [usr_name, setUsr_name] = useState()
    const history = useHistory()
    const [loader, setLoader] = useState(false)
    
    useEffect(() => {
    }, [])

    const G_SigniIn = async()=>{
        const date = new Date()
        const utc1 = Date.UTC(date.getFullYear(), date.getMonth(), date.getDate())
        const result = await auth.signInWithPopup(Gauth)
        const query = await fb.firestore().collection("users_profile").where("id", "==", result.user.uid).get();
        if (query.docs.length === 0) {
            result.user.updateProfile({
                photoURL:'https://i.pinimg.com/originals/0d/dc/ca/0ddccae723d85a703b798a5e682c23c1.png'
                }).then(()=>{fb.firestore().collection('users_profile').doc(result.user.uid).set({
                f_name : 'First Name',
                l_name : 'Last Name',
                id:result.user.uid,
                plan:{},
                join_date : utc1,
                new_user:true,
                wish_list:[]
                })
            }).then(()=>{
                history.push('/')})  
        }
    }
    const SignIn = ()=>{
        setLoader(true)
        if(paswrd==cpaswrd){
            const date = new Date()
            const utc1 = Date.UTC(date.getFullYear(), date.getMonth(), date.getDate())
            auth.createUserWithEmailAndPassword(email,paswrd).then((user)=>{
                if(user){
                    user.user.updateProfile({
                        displayName:usr_name,
                        photoURL:'https://i.pinimg.com/originals/0d/dc/ca/0ddccae723d85a703b798a5e682c23c1.png'
       
                    }).then(()=>{fb.firestore().collection('users_profile').doc(user.user.uid).set({
                        id:user.user.uid,
                        f_name : fname,
                        l_name : lname,
                        name : fname+' '+lname,
                        plan:{},
                        join_date : utc1,
                        new_user:true,
                        wish_list:[]
                    }).then(()=>{
                        history.push('/')
                        setLoader(false)
                    })})
                }
            }).catch((err)=>{
                alert(err)
                setLoader(false) 
                setEmail('')
                setPaswrd('')
                setCpaswrd('')   
            }) 
        }else{
            alert('Password Doesnt Match')
            setPaswrd('')
            setCpaswrd('') 
        }
        
        
    }
    return (
        <div className='section'>
            <div className='containor'>
                {loader && <Loader/>}
                <div className='logo-box'>
                    <div className='logo-signup'>
                    <img className='logo-img' src={logo} alt="Netflix logo"/>
                    </div>
                </div>
                <div className='signup-box'>
                    <div className='card-signup'>
                        <div className='card-details'>
                            <div className='card-heading'>
                                <h1>Sign Up</h1>
                            </div>
                            <div className='details'>
                                <div className='input-box'>
                                    <input autoComplete='off' type='email' placeholder='Email' name='email' value={email} onChange={e=>setEmail(e.currentTarget.value)}></input>
                                </div>
                                <div className='name-box input-box'>
                                    <input autoComplete='off' type='text' placeholder='First Name' name='fname' className='name' value={fname} onChange={e=>setFname(e.currentTarget.value)}></input>
                                    <input autoComplete='off' type='text' placeholder='Last Name' name='lname' className='name name2' value={lname} onChange={e=>setLname(e.currentTarget.value)}></input>
                                </div>
                                <div className='username input-box'>
                                    <input autoComplete='off' type='text' placeholder='User Name' name='usr_name' value={usr_name} onChange={e=>setUsr_name(e.currentTarget.value)}></input>
                                </div>
                                <div className='pswrd input-box'>
                                    <input autoComplete='off' type='password'placeholder='Password' name='paswrd' value={paswrd} onChange={e=>setPaswrd(e.currentTarget.value)}/>  
                                </div>
                                <div className='pswrd_2 input-box'>
                                    <input autoComplete='off' type='password'placeholder=' Confirm Password' name='c_paswrd' value={cpaswrd} onChange={e=>setCpaswrd(e.currentTarget.value)}></input>
                                </div>
                                <div className='sub-button'>
                                    <input type='submit' value="Let's Chill!"  onClick={SignIn}/>
                                </div>
                                <div className='social'>
                                    <div className='social-line'>
                                        <hr className='line'></hr>
                                        <p>or</p>
                                        <hr className='line'></hr>
                                    </div>
                                    <div className='social-icons'>
                                        <SocialIcon network='google' bgColor='#111' fgColor='#bc1823'  className='scl-icn' onClick={G_SigniIn} /> 
                                        <SocialIcon network='facebook'  bgColor='#111' fgColor='#bc1823' className='scl-icn' onClick={()=>{history.push('/404_feature')}} />                    
                                    </div>
                                </div>
                                <div className='signup-link'>
                                    <p>A ZedFlix Member, then <Link to='/login'>SignIN</Link></p> 
                                </div>
                                
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default SignUp
