import React, { useEffect, useState } from 'react'
import GooglePayButton from '@google-pay/button-react'
import '../../Pages/GPay/GPay.css'
import { fb, auth } from '../../Firebase/config'
import NavBar from '../../Components/NavBar/NavBar'
import { useHistory } from 'react-router'

function GPay() {
  const plan_name = window.location.hash.split('/')[2]
  const [plan, setPlan] = useState('')
  const history = useHistory()

  const handlePayment = ()=>{
    const user = auth.currentUser;
    const date = new Date()
    const utc1 = Date.UTC(date.getFullYear(), date.getMonth(), date.getDate())
    plan['sub_date'] = utc1
    fb.firestore().collection('users_profile').doc(user ? user.uid : '').update({plan:plan,new_user:false}).then(()=>{
      alert(`${plan_name} plan subscribed. Thank you.`)
      history.push('/')
      
    })
    

  }
  useEffect(() => {
      fb.firestore().collection('Plans').doc(plan_name).get().then((snapshot)=>{setPlan(snapshot.data())})
  }, [])
    return (
        <div className='pay-cont'>
              <NavBar notHome></NavBar>
              <div className='pay-heading'>
                <h2>Your Order</h2>
              </div>
          <div className='pay-box'>     
            <div className='pay-det'>     
              <div className='pay-items'>
                <div className='pay-items-1'>
                  <p>Items to checkout  :  </p>
                </div>       
                <div className='pay-items-2'>
                  <p> {plan_name} Subscription Plan </p>
                  <hr className='line'></hr>
                  <p>Total : ${plan ? plan.price : ''} </p>
                </div>
                
              </div>
              {plan_name=='Free' ? 
              <div className='gpay_but'>
                <input type='submit' onClick={()=>handlePayment()} value='Subscribe'/>
              </div>
              :
              <div className='gpay_but'>
              <GooglePayButton
                  environment="TEST"
                  paymentRequest={{
                    apiVersion: 2,
                    apiVersionMinor: 0,
                    allowedPaymentMethods: [
                      {
                        type: 'CARD',
                        parameters: {
                          allowedAuthMethods: ['PAN_ONLY', 'CRYPTOGRAM_3DS'],
                          allowedCardNetworks: ['MASTERCARD', 'VISA'],
                        },
                        tokenizationSpecification: {
                          type: 'PAYMENT_GATEWAY',
                          parameters: {
                            gateway: 'example',
                            gatewayMerchantId: 'exampleGatewayMerchantId',
                          },
                        },
                      },
                    ],
                    merchantInfo: {
                      merchantId: '12345678901234567890',
                      merchantName: 'Demo Merchant',
                    },
                    transactionInfo: {
                      totalPriceStatus: 'FINAL',
                      totalPriceLabel: 'Total',
                      totalPrice: '100.00',
                      currencyCode: 'USD',
                      countryCode: 'US',
                    },
                  }}
                  onLoadPaymentData={()=>{handlePayment()}}
              >

              </GooglePayButton>
              </div>
              }
             
            </div>
          </div>
            
        </div>
    )
}

export default GPay;
