import React, { useEffect, useState } from 'react'
import NavBar from '../../Components/NavBar/NavBar'
import '../../Pages/Subscription/Subscription.css'
import {ImCheckmark} from 'react-icons/im'
import {ImCross} from 'react-icons/im'
import { useHistory } from 'react-router-dom'
import { fb, auth } from '../../Firebase/config'
import Loader from '../../Components/Loader/Loader'
function Subscription() {

    const history = useHistory()
    const [cur_user, setCur_usr] = useState()
    const [plans, setPlans] = useState([])
    const [loader, setLoader] = useState(true)

    const Subscribe = (plan)=>{
        history.push(`subscribe/${plan}`)
    }

    useEffect(() => {
        auth.onAuthStateChanged((user) => {
            if(user){
                fb.firestore().collection('users_profile').doc(user.uid).get().then((snapshot)=>{setCur_usr(snapshot.data())})
               
            }})
        fb.firestore().collection("Plans").orderBy("expiry", "asc").get().then((snapshot)=>{
            setPlans(snapshot.docs)
            setLoader(false)
        })
         
    }, [])
   
    return (
        <div className='sub-cont'>
            {loader && <Loader/>}
            <NavBar notHome></NavBar>
            {cur_user?
            <div className='sub-containor'>
            
            <div className='sub-active-plan-top'>
                <div className='sub-active-plan'>
                    <div className='sub-heading'>
                        <h1>Current Plan</h1>
                    </div>
                    <div className='sub-active-plan-cards'>
                        
                        {cur_user.plan.name?
                            <div className='sub-card'>
                            <div className='sub-card-heading'>
                                <h2> {cur_user.plan.name} </h2>                              
                            </div>
                            <div className='sub-detail'>
                                <ol>
                                    <li><ImCheckmark></ImCheckmark>   Unlimited Movies and Shows</li>
                                    <li><ImCheckmark></ImCheckmark>  {cur_user.plan.features['devices']} Devices</li>
                                    {cur_user.plan.features['download']? <li><ImCheckmark/>  Download available</li>:<li><ImCross></ImCross>  No Download available</li>}
                                    <li><ImCheckmark></ImCheckmark>  {cur_user.plan.features['quality']}</li>
                                    <li><ImCheckmark></ImCheckmark>  {cur_user.plan.features['validity']}</li>
                                </ol>
                            </div>
                            <div className='price'>
                                <h2>$ {cur_user.plan ? cur_user.plan.price : ''} </h2>
                            </div>
                            <div className='sub-card-but'>
                                <input type='submit' value='Registered'></input>
                            </div>
                        </div>
                        :
                        <div className='no-plan'>
                            <h1>No Active Plan</h1>
                        </div>}
                        
                        
                    </div>
                </div>
            </div>
            <div className='sub-avlble-plan-top'>
                <div className='sub-avlble-plan'>
                    <div className='sub-heading'>
                        <h1>Available Plan</h1>
                    </div>
                    <div className='sub-active-plan-cards avlble-plan-cards'>
                        {plans.map((obj)=>
                        obj.data().name!='Free' || cur_user.new_user? 
                        <div className='sub-card'>
                            <div className='sub-card-heading'>
                                <h2>{obj.data().name}</h2>                              
                            </div>
                            <div className='sub-detail'>
                                <ol>
                                    <li><ImCheckmark></ImCheckmark>   Unlimited Movies and Shows</li>
                                    <li><ImCheckmark></ImCheckmark>   {obj.data().features['devices']} Devices</li>
                                    {obj.data().features['download']? <li><ImCheckmark/>  Download available</li>:<li><ImCross></ImCross>  No Download available</li>}
                                    <li><ImCheckmark></ImCheckmark> Upto {obj.data().features['quality']}  </li>
                                    <li><ImCheckmark></ImCheckmark>  {obj.data().features['validity']}</li>
                                </ol>
                            </div>
                            <div className='price'>
                                <h2>${obj.data().price}</h2>
                            </div>
                            <div className='sub-card-but'>
                                <input onClick={()=>{Subscribe(obj.data().name)}} type='submit' value='Register'></input>
                            </div>
                        </div> 
                        :'')}
                    </div>
                </div>
            </div>  
        </div>
        :''}
        </div>
        
    )
}

export default Subscription
