
import './App.css'
import {Switch,Route} from "react-router-dom";
import Movie from './Pages/Specific Movie/Movie'
import Home from './Pages/Home/Home';
import Player from './Components/Player/Player';
import Tv from './Pages/Specific Movie/Tv';
import Epsiodes from './Pages/Episodes/Epsiodes';
import Episode from './Pages/Specific Movie/Episode';
import Notfound_404 from './Pages/404/Notfound_404';
import SignUp from './Pages/Signup/SignUp'
import Login from './Pages/Login/Login';
import UserProfile from './Pages/User Profile/UserProfile';
import WishList from './Pages/Whish List/WishList';
import ReAuth from './Components/Re Authentication/ReAuth';
import Subscription from './Pages/Subscription/Subscription';
import GPay from './Pages/GPay/GPay';
import Feature_404 from './Pages/404/Feature_404';
import Profile from './Pages/User Profile/Profile';

function App() {
  return (
    <div className="app">
      <Switch>
      <Route path='/' exact>
      <Home></Home>
      </Route>
      <Route path='/movies'>
      <Movie></Movie>
      </Route>
      <Route path='/tv'>
      <Tv></Tv>
      </Route>
      <Route path='/play_movie'>
      <Player></Player>
      </Route>
      <Route path='/play_tv'>
      <Player isTv></Player>
      </Route>
      <Route path='/episodes'>
        <Epsiodes></Epsiodes>
      </Route>
      <Route path='/episode/:id/:id/:id' exact >
      <Episode></Episode>
      </Route>
      <Route path='/signup' >
      <SignUp></SignUp>
      </Route>
      <Route path='/login'>
      <Login></Login>
      </Route>
      <Route path='/404'>
      <Notfound_404></Notfound_404>
      </Route>
      <Route path='/404_feature'>
      <Feature_404></Feature_404>
      </Route>
      <Route path='/user_profile'>
       <UserProfile></UserProfile> 
      </Route>
      <Route path='/wish_list'>
        <WishList></WishList>
      </Route>
      <Route path='/re_auth_mailchange'>
         <ReAuth path='/user_profile' tochange='Email'></ReAuth>
      </Route>
      <Route path='/subscription'>
        <Subscription></Subscription>
      </Route>
      <Route path='/subscribe'>
          <GPay></GPay>
      </Route>
      <Route path='/profile'>
          <Profile/>
      </Route>

      </Switch>
      
  </div>
  );

}

export default App;



