import React, { useEffect, useState } from 'react'
import './Banner.css'
import axios from 'axios'
import {imageUrl} from '../../Constants'
import { useHistory } from 'react-router-dom'
import { fb, auth } from '../../Firebase/config'
import firebase from 'firebase'
import Notification from '../Notify/Notify'
import ReactNotification from 'react-notifications-component'
import 'react-notifications-component/dist/theme.css'
import Loader from '../Loader/Loader'
function Banner(props) {
    const [movie, setMovie] = useState('')
    const Str = require('@supercharge/strings')
    const [desc, setDesc] = useState('')
    const history = useHistory()
    const [user, setUser] = useState(null)
    const [wish_movie, setWish_movie] = useState('')
    const [inlist, setinList] = useState(false)
    const [loader, setLoader] = useState(true)
 
    useEffect(() => {
        
        auth.onAuthStateChanged((user)=>{
            if(user){
                fb.firestore().collection('users_profile').doc(user.uid).get().then((snapshot)=>{
                    setUser(snapshot.data())
                    if (props.isHome){
                        axios.get(props.url).then((response)=>{ 
                            const min = 0
                            const max = 20
                            const rand_int = Math.floor(min+Math.random()*(max-min))
                            setMovie(response.data.results[rand_int])
                            const overview = Str(response.data.results[rand_int].overview).limit(250,'...').get()
                            setDesc(overview)
                            listCheck(response.data.results[rand_int],snapshot.data())
                            setLoader(false)
                            

                            
                        })
                    }else{
                        axios.get(props.url).then((response)=>{ 
                            setMovie(response.data)
                            const overview = Str(response.data.overview).limit(250,'...').get()
                            setDesc(overview)
                            listCheck(response.data,snapshot.data())
                            setLoader(false)
                        })
                    }          
                })
            }
            })
        
    }, [])


    const handlePlay = (id,epi,ssn)=>{
        if (props.isEpi){
            const tv_id = window.location.hash.split('/')[2]
            history.push(`/play_tv/${tv_id}/${ssn}/${epi}`)
        }else{
            history.push(`/play_movie/${id}`)

        }
    }
    const listCheck=(movie,user)=>{
        if(user){
            user.wish_list.map((obj)=>{
                if(obj.id == movie.id){
                    setinList(true)
                    setWish_movie(obj)
                    
                }
            })
        }
    }
    const handleListOut = ()=>{
        setLoader(true)
        fb.firestore().collection('users_profile').doc(auth.currentUser.uid).update({
           'wish_list' : firebase.firestore.FieldValue.arrayRemove(wish_movie)
        }).then(()=>{
            Notification('Succesfuly removed from wishlist!','succes')
            setLoader(false)
        }).catch(err=>{Notification(err,'sucess')})
        setinList(false)
    }
    const handleListIn = ()=>{
        setLoader(true)
        fb.firestore().collection('users_profile').doc(auth.currentUser.uid).update({wish_list:firebase.firestore.FieldValue.arrayUnion(movie)}).then(()=>
        {Notification('Succesfuly added to wishlist!','succes')
        setLoader(false)
        }).catch(err=>{Notification(err,'sucess')})
        setinList(true)
        
    }

    return (
        <div className='banner_in' style={{backgroundImage:`url(${movie.still_path ? imageUrl+movie.still_path : movie ? imageUrl+movie.backdrop_path:''})`}}>
            <div  className='banner-containor'>
            {loader ? <Loader />:''}
            <ReactNotification/>
            {loader? '':
            <div className='content'>
                <h1 className='title' onClick={()=>history.push(`movies/${movie.id}`)}>{ movie.name ? movie.name: movie.title}</h1>
                
                <div className='desc'><p className='desc'> {movie ? desc: ''} </p></div>
                
                <div className='banner_butns'>
                    {user ? user.plan.name ? props.isSrs ? props.isEpi ? <button onClick={()=>handlePlay(movie.id, movie.episode_number, movie.season_number)} className='button'>Play</button> : ''  : <button onClick={()=>handlePlay(movie.id)} className='button'>Play</button>: <button onClick={()=>history.push('/subscription')} className='button'>Subscribe</button>: '' }
                {inlist ? <button className='button' onClick={()=>{handleListOut()}}>Added to Wish list</button>
                :  
                <button className='button' onClick={handleListIn}>Add to Wish list</button>  }
                    
                </div>
                
            </div> 
            }
            
            <div className="fade"></div>
            
            
           
        </div>
        </div>
        
    )
}

export default Banner

