import React, { useState, useEffect } from 'react'
import '../Login/Login.css'
// import logo from '../../Components/NavBar/Zedflix2.png'
import { SocialIcon } from 'react-social-icons'
import { auth, fb, Gauth } from '../../Firebase/config'
import { useHistory, Link } from 'react-router-dom'
import Loader from '../../Components/Loader/Loader'
import logo from '../Login/logo.png'
 
function Login() {

    const [email, setEmail] = useState('')
    const [paswrd, setPaswrd] = useState('')
    const history = useHistory()
    const [loader, setLoader] = useState(false)

    useEffect(() => {
        auth.onAuthStateChanged((user) => {
            if (user) {
              history.push('/')
              
    
          }});
    }, [])

    const G_SigniIn = async()=>{
        const date = new Date()
        const utc1 = Date.UTC(date.getFullYear(), date.getMonth(), date.getDate())
        const result = await auth.signInWithPopup(Gauth)
        const query = await fb.firestore().collection("users_profile").where("id", "==", result.user.uid).get();
        if (query.docs.length === 0) {
            result.user.updateProfile({
                photoURL:'https://i.pinimg.com/originals/0d/dc/ca/0ddccae723d85a703b798a5e682c23c1.png'
                }).then(()=>{fb.firestore().collection('users_profile').doc(result.user.uid).set({
                f_name : 'First Name',
                l_name : 'Last Name',
                id:result.user.uid,
                plan:{},
                join_date : utc1,
                new_user:true,
                wish_list:[]
                })
            }).then(()=>{
                history.push('/')
                }) 
        }
    }


    const Login = ()=>{
        setLoader(true)
        auth.signInWithEmailAndPassword(email,paswrd).then(()=>
            {history.push('/')
            setLoader(false)}
        ).catch(err=>{
            setLoader(false)
            alert(err)
            setEmail('')
            setPaswrd('')
        })
    }

    return (
        <div className='section'>
            {loader ? <Loader/>: 
                <div className='containor'>
                
                <div className='logo-box'>
                    <div className='logo-signup'>
                    <img className='logo-img' src={logo} alt="Netflix logo"/>
                    </div>
                </div>
                <div className='signup-box'>
                    <div className='card-signup card-login'>
                        <div className='card-details'>
                            <div className='card-heading'>
                                <h1>Sign In</h1>
                            </div>
                            <div className='details'>
                                <div className='username input-box'>
                                    <input autoComplete='off' type='text' placeholder='User Name' name='usr_name' value={email} onChange={e=>setEmail(e.currentTarget.value)}></input>
                                </div>
                                <div className='pswrd input-box'>
                                    <input autoComplete='off' type='password'placeholder='Password' name='paswrd' value={paswrd} onChange={e=>setPaswrd(e.currentTarget.value)}></input>  
                                </div>
                                <div className='sub-button'>
                                    <input type='submit' value="Let's Chill!" onClick={Login} ></input>
                                </div>
                                <div className='social-line'>
                                    <hr className='line'></hr>
                                    <p>or</p>
                                    <hr className='line'></hr>
                                </div>
                                <div className='social-icons'>
                                    <SocialIcon network='google' bgColor='#111' fgColor='#bc1823'  className='scl-icn' hoverColor='#111' onClick={G_SigniIn} /> 
                                    <SocialIcon network='facebook'  bgColor='#111' fgColor='#bc1823'  className='scl-icn' onClick={()=>{history.push('/404_feature')}}  />                   
                                </div>
                                <div className='signup-link'>
                                    <p>Not ZedFlix Member. <Link to='/signup'>SignUP</Link> Now.</p> 
                                </div>
                                                         
                            </div>
                        </div>
                    </div>
                </div>
            </div>    
            }
            
        </div>
    )
}


export default Login


