import { useCallback, useState } from 'react'
import Cropper from 'react-easy-crop'
import '../Crop/Crop.css'
import {getCroppedImg} from './CropImage'
import {ImCross} from 'react-icons/im'

function Crop(props){
  const [crop, setCrop] = useState({ x: 0, y: 0 })
  const [zoom, setZoom] = useState(1)
  const [croppedAreaPixels, setCroppedAreaPixels] = useState()
  const onCropComplete = useCallback((croppedArea, croppedAreaPixels) => {
    setCroppedAreaPixels(croppedAreaPixels)
  }, [])
  const Save_img= async ()=>{
    const croppedImage = await getCroppedImg(
        props.imgUrl,
        croppedAreaPixels
    )
    props.getBlob(croppedImage)
  }
  const Close = ()=>{
    props.loader2(false)
    // document.getElementsByClassName('crop-cont')[0].style.display = 'none'
    props.imageUrl('')
  }
  return (
      <div className='crop-cont'>
          <div className='crop-top'>
          </div>
          <div className='crop-box'>
            <div className='crop-close'>
              <ImCross onClick={Close}/>
            </div>
            
              <div className='cropper'>
                <Cropper
                    image={props.imgUrl}
                    crop={crop}
                    zoom={zoom}
                    aspect={4 / 3}
                    onCropChange={setCrop}
                    onCropComplete={onCropComplete}
                    onZoomChange={setZoom}
                />
              </div>
              <div className='crop-btns'>
                  <button onClick={()=>{Save_img()}} className='crop-btn'>Submit</button>
              </div>
            
          </div>
      
      </div>
      
    
  )
}
export default Crop;